	import {
		getHomeClassList,
		getHomeGoodsList
	} from "@/api/home.js";
	const classBgcolor = ["#FFB6B3", "#FFE566", "#A7FFDD", "#9AD6FF", "#61D6C9", "#A5F598"];


	export default {
		data() {
			return {
				classList: [], //商品分类
				goodsList: [], //商品列表
                isLazyDown:false, //判断是否到底部了
				GoodsQueryInfo: {
					pageNumber: 1,
					pageSize: 10,
				},
				ClassQueryInfo: {
					pageNumber: 1,
					pageSize: 6,
				}
			};
		},
		onShow() {

		},
		async created() {
			await this.getClassList()
			await this.getGoodsList()

		},
		methods: {
			//跳转详情
			goDetail(id) {
				uni.navigateTo({
					url:`/pages/goodsDetail/goodsDetail?id=${id}`,
				});
			},
			//获取分类
			getClassList() {
				getHomeClassList(this.ClassQueryInfo).then(res => {
					this.classList = res.data
					this.classList.forEach((v, i) => {
						v.color = classBgcolor[i]
					})
				})
			},
			//获取商品
			getGoodsList() {
				getHomeGoodsList(this.GoodsQueryInfo).then(res => {
					this.goodsList = res.data
				})
			}
		},

		onReachBottom(e) {
			if(this.isLazyDown){
				return  uni.showToast({
					title:'到底了...',
					icon:'none'
				})
			}
			this.GoodsQueryInfo.pageNumber=this.GoodsQueryInfo.pageNumber+1
			uni.showLoading({
				title: "加载中...",
				success: () => {
					getHomeGoodsList(this.GoodsQueryInfo).then(res => {
						this.goodsList = this.goodsList.concat(res.data)
						if(res.data.length==0){
							this.isLazyDown=true
						}
						uni.hideLoading()
					})
				},
			});
		},
	};
